import { environment } from "@rp/common/environment"
import { EnvironmentConfig } from "@rp/common/environment/types"

type ApiType = "bigdata" | "text-analytics" | "web-socket" | "proxy" | "agents"

const apiEnvKeyMap: Record<ApiType, keyof EnvironmentConfig["app"]> = {
  proxy: "proxyUrl",
  bigdata: "apiUrl",
  "text-analytics": "taUrl",
  "web-socket": "wsUrl",
  agents: "agentsUrl",
}

const getApiEnvironmentConfig = (apiType: ApiType = "bigdata") => {
  const defaultUrl = environment.app[apiEnvKeyMap[apiType]]
  return { name: environment.name, url: defaultUrl }
}

export { getApiEnvironmentConfig }
